

import Vue from 'vue'
import FastClick from 'fastclick'
import VueRouter from 'vue-router'
import AjaxPlugin from 'vux/src/plugins/ajax/index.js'
import ToastPlugin from 'vux/src/plugins/toast/index.js'
import cookie from 'vux/src/tools/cookie/index.js'

import App from './App'
import Home from './view/Home'
import logins from './components/Hello'
import List from './components/List'
import Open from './components/OpenIfram'
import Login from './components/Login'
import Vuex from 'Vuex'
import vuexI18n from 'vuex-i18n'
import javaApi from './common/js/config'

Vue.use(VueRouter)
Vue.use(Vuex)
Vue.use(vuexI18n)
Vue.use(AjaxPlugin)
Vue.use(ToastPlugin)


Vue.prototype.javaApi=javaApi

const routes = [{
path: '/',
component: Home,
name: 'Home',
children: [
{ path: 'login', component: Login },
]
},
{
path: '/h',
component: logins,
name: 'h',
},
{
path: '/login',
component: Login,
name: 'login'
},
{
path: '/list',
component: List,
name: 'list',
meta: {
keepAlive: false // 不需要被缓存
}
},
{
path: '/open',
component: Open,
name: 'open',
meta: {
requireAuth: true,  // 添加该字段，表示进入这个路由是需要登录的
}
}
]

const router = new VueRouter({
routes
})
const store = new Vuex.Store({}) // 这里你可能已经有其他 module

Vue.use(vuexI18n.plugin, store)
const translationsEn = {
'content': 'This is some {type} content'
}
const translationsDe = {
'My nice title': 'Ein schöner Titel',
'content': 'Dies ist ein toller Inhalt'
}
Vue.i18n.add('en', translationsEn)
Vue.i18n.add('de', translationsDe)


Vue.i18n.set('en')

store.registerModule('vux', { // 名字自己定义
state: {
isLoading: false
},
mutations: {
updateLoadingStatus (state, payload) {
state.isLoading = payload.isLoading
}
}
})
FastClick.attach(document.body)

Vue.config.productionTip = false
router.beforeEach(function (to, from, next) {

if (to.meta.requireAuth) {  // 判断该路由是否需要登录权限
if (cookie.get('tokenssss')) {  // 通过vuex state获取当前的token是否存在
next();
}
else {
console.log(to.params.id)
next({
path: '/login',
query: {redirect: to.fullPath,redirecturl: to.params.id}

})
}
}
else {
store.commit('updateLoadingStatus', {isLoading: true})
next()
}
})

router.afterEach(function (to) {
store.commit('updateLoadingStatus', {isLoading: false})
})
/* eslint-disable no-new */
new Vue({
router,
store,
render: h => h(App)
}).$mount('#app-box')


