
import Panel from 'vux/src/components/panel/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Radio from 'vux/src/components/radio/index.vue'
import cookie from 'vux/src/tools/cookie/index.js'
import LoadMore from 'vux/src/components/load-more/index.vue'


export default {
name: 'list',
components: {
Panel,
Group,
Radio,
LoadMore
},
methods: {
/*檢測滾動*/
handleScroll: function () {
var productMain = this.$refs.listMain
if(document.body.scrollHeight<=document.documentElement.scrollTop+document.documentElement.clientHeight){
if(this.flag){
this.flag=false
this.loading=true
this.page++
this.ajax(0,this.page)
}
}
},
ajax: function (zoneId,page) {
var page=page
var zoneId=zoneId
if(!page){
page=1
}
if(!zoneId){
zoneId=''
}
/*请求*/
this.$http.post(this.javaApi+'/weixinWeb/product/list',
{
"versionId": "2.0",
"flag": "2",
"page": page,
"zoneId": zoneId
},
{
headers:{

},
emulateJSON: false
}
).then((response) => {
if(response.data.returnCode == 200) {
console.log(response.data.data)
var items=response.data.data.list

Object.keys(items).forEach(key => {
items[key].links={name:'list',params:{id:items[key].zoneId}}
this.todos.push(items[key])
})
var that=this
if(response.data.data.list.length==0){
that.loading=false
this.tip="没有更多了！"
}else{
setTimeout(function(){
that.loading=false
that.flag=true
},1000)
}

}
}).catch(function(response) {
console.log(response)
})
}
},
mounted () {
window.addEventListener('scroll', this.handleScroll);
},
created: function() {
var id = this.$route.params.id?this.$route.params.id:''
if(id==''){
this.ajax()
}else{
console.log(id)
this.ajax(id)
}
},
data () {
return {
todos: [],
loading: false,
page: 1,
flag: true,
tip: ''
}
}
}



