
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'



export default {
name: 'productList',
components: {
Grid,
GridItem
},
data () {
return {
todos: []
}
},
methods: {
onItemClick () {
console.log('on item click')
}
},
created: function() {
var timestamp=Date.parse(new Date())
/*请求*/
this.$http.post(this.javaApi+'/weixinWeb/product/queryTopWoolen',
{
"timestamp": timestamp
},
{
headers:{

},
emulateJSON: false
}
).then((response) => {
if(response.data.returnCode == 200) {
var items=response.data.data.zoneProductList
Object.keys(items).forEach(key => {
items[key].links={name:'list',params:{id:items[key].zoneId}}
this.todos.push(items[key])
})

}
}).catch(function(response) {
console.log(response)
})
}
}



