
import Loading from 'vux/src/components/loading/index.vue'

import { mapState } from 'Vuex'
import format from './assets/js/setRem'

export default {
name: 'App',
components: {
Loading
},
computed: {
...mapState({
isLoading: state => state.vux.isLoading
})
},
created: function (){
console.log(format)
format.setRem()
}
}



